<template class="billet mt-4">
  <div class="container">
    <div class="info-carte" v-if="this.selectedSize == '' && this.selectedType == ''">
      <p>N'oubliez pas de filter notre carte !</p>
    </div>
    <div class="filtre">
      <div class="filtre1 mt-3">
        <p>Scene</p>
        <select class="form-control" v-model="selectedType">
          <option
            :value="scene.value"
            v-for="scene in scenes"
            :key="scene.value"
          >
            {{ scene.value }}
          </option>
        </select>
      </div>

      <div class="filtre2 mt-3">
        <p>Lieu</p>
        <select class="form-control" v-model="selectedSize">
          <option :value="lieu.value" v-for="lieu in lieux" :key="lieu.value">
            {{ lieu.value }}
          </option>
        </select>
      </div>
      <div class="reset">
        <button
          class="btn mb-3"
          style="background-color: rgb(118, 115, 254) !important; color: white"
          @click="shut()"
        >
          Reset
        </button>
      </div>
    </div>

